@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input[type='text'],
input[type='button'] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input,
input:focus {
  outline: none;
  box-shadow: none;
}

html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/triviaApp/static/media/Background.b70c56a3.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  height: 100%;
  background-size: cover;
  letter-spacing: 1.1px;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4 {
  color: white;
}

.standard-button {
  font-size: 24px;
  width: 200px;
  color: white;
  height: 56px;
  border: 1px solid #00e7d0;
  border-radius: 6px;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  background: transparent;
  outline: none;
  margin: auto;
}

.standard-button:disabled {
  color: white;
  border-color: #3e455d;
}

@media only screen and (max-width: 480px) {
  .standard-button {
    height: 48px;
  }
}

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding: 5%;
  min-height: 640px;
}

.main-container {
  height: 600px;
  width: 100%;
  max-width: 800px;
  margin-right: 48px;
  background-image: -webkit-linear-gradient(45deg, #20263b, #353f60);
  background-image: linear-gradient(45deg, #20263b, #353f60);
  border-radius: 6px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.side-container {
  height: 600px;
  width: 400px;
  min-width: 320px;
  background-color: rgb(0, 0, 0, 0.5);
  border-radius: 6px;
  background-image: -webkit-linear-gradient(45deg, #20263b, #353f60);
  background-image: linear-gradient(45deg, #20263b, #353f60);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.start-content {
  width: 100%;
}

.start-content h1 {
  font-weight: black;
  font-size: 64px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.start-content h2 {
  font-size: 24px;
  font-weight: 300;
  line-height: 30px;
  margin-top: 16px;
  margin-bottom: 80px;
}

@media only screen and (max-width: 1200px) {
  .start-content h1 {
    font-size: 40px;
  }
  .start-content h2 {
    font-size: 18px;
  }
}

@media only screen and (min-width: 800px) {
  .start-content input:hover {
    background-color: #1fa59d;
    border-color: #00e7d0;
  }
}

@media only screen and (max-width: 800px) {
  body,
  html,
  .main-container,
  .grid-container {
    overflow: hidden !important;
  }

  .grid-container {
    padding: 0;
    min-height: 0;
  }

  .main-container {
    height: 100%;
    width: 100%;
    border-radius: 0px;
    margin: 0;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
  }

  .start-content h1 {
    font-size: 44px;
  }

  .side-container {
    display: none;
  }
}

@media only screen and (max-width: 480px) {
  .start-content h1 {
    font-size: 32px;
  }
}
.btn-answer {
  width: 100%;
  color: #fff;
  min-height: 56px;
  font-size: 16px;
  text-align: left;
  padding-left: 24px;
  margin: 16px auto;
  font-weight: 500;
  letter-spacing: 1.1px;
  border: 1px solid #585757;
  border-radius: 25px;
  background: transparent;
  cursor: pointer;
}



.correctAnswer {
  background-color: #1fa59d;
  border-color: #1fa59d;
  color: white;
}

.incorrectAnswer {
  background-color: #a93e69;
  border-color: #a93e69;
  color: white;
}

@media only screen and (min-width: 800px) {
  .btn-answer:hover {
    border-color: #1fa59d;
  }
}

@media only screen and (max-width: 480px) {
  .btn-answer {
    min-height: 48px;
    font-size: 13px;
    margin: 12px auto;
    padding-left: 12px;
  }
}

.question-content {
  width: 100%;
  height: 100%;
}

.question-header {
  background-image: -webkit-linear-gradient(45deg, #8147fd, #ab75ff);
  background-image: linear-gradient(45deg, #8147fd, #ab75ff);
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  height: 48px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.question-header h4 {
  font-size: 16px;
  padding-left: 24px;
  font-weight: 300;
}

.question-container {
  width: 90%;
  min-height: 80px;
  margin: 24px auto;
  letter-spacing: 1.1px;
  display: flex;
  justify-content: center;
}

.question-container h2 {
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

.answer-container {
  width: 90%;
  margin: auto;
}

@media only screen and (max-width: 800px) {
  .question-content {
    padding-bottom: 64px;
    overflow: auto;
  }

  .question-container h2 {
    font-size: 20px;
    line-height: 28px;
  }

  .question-header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    border-radius: 0px;
  }
}

@media only screen and (max-width: 480px) {
  .question-container {
    min-height: 64px;
  }
  .question-container h2 {
    font-size: 16px;
    line-height: 22px;
    min-height: 64px;
  }

  .question-header h4 {
    font-size: 14px;
  }
}

.countdown-content {
  width: 100%;
}

.countdown-content h1 {
  font-weight: black;
  font-size: 64px;
  margin-top: 24px;
}

.countdown-content h2 {
  font-size: 24px;
  font-weight: 300;
}

.summary-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.points-button {
  border-color: #00e7d0;
  width: 200px;
  font-size: 24px;
  margin-top: 80px;
}

.summary-content h1 {
  font-weight: black;
  font-size: 64px;
  margin-top: 24px;
}

.summary-content h2 {
  font-size: 24px;
  font-weight: 300;
}

.leaderboard-confirm {
  width: 90%;
  display: flex;
  flex-direction: column;
}

.leaderboard-confirm h1 {
  font-size: 36px;
  font-weight: 500;
  line-height: 48px;
}

.leaderboard-confirm h2 {
  font-size: 20px;
  font-weight: 300;
  margin-top: 48px;
}

.name-entry {
  margin: 24px auto 40px auto;
  height: 56px;
  width: 280px;
  border-radius: 6px;
  border: 1px solid #1fa59d;
  background-color: #38686d;
  padding-left: 12px;
  color: white;
  font-size: 24px;
  caret-color: #1fa59d;
  outline: none;
  letter-spacing: 1.2px;
}

@media only screen and (min-width: 900px) {
  .points-button:hover {
    background-color: #1fa59d;
  }
}

@media only screen and (max-width: 900px) {
  .leaderboard-confirm h2 {
    font-size: 16px;
  }

  .leaderboard-confirm input {
    width: 90%;
  }
}

@media only screen and (max-width: 400px) {
  .leaderboard-confirm h1 {
    font-size: 32px;
  }
}
.loadingContainer {
  width: 100%;
  height: 440px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.leaderboardTable {
  width: 90%;
  display: relative;
  height: 100%;
  margin: -20px auto;
}

.leaderboardContainer h3 {
  color: white;
  padding-top: 24px;
  width: 90%;
  font-size: 24px;
  font-weight: lighter;
  margin: auto;
}

.entryStyling h3 {
  font-size: 16px;
}

.entryStyling {
  font-size: 16px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  color: white;
  align-items: center;
  border-bottom: solid 1px #3e455d;
}

.entryStyling:last-child {
  border-bottom: 0px;
}

.entryStyling h3:first-child {
  font-size: 16px;
  padding-left: 0;
  width: 48px;
  padding-right: 16px;
  font-weight: bold;
  color: #646b81;
}

.entryStyling h3:last-child {
  color: #7cffcb;
  text-align: right;
  padding-right: 0;
}

.entryStyling h3:nth-child(2) {
  text-align: left;
  width: 100%;
}

.side-container-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.point-container {
  height: 200px;
  border-bottom: 1px solid #3e455d;
}

.timer-container {
  height: 300px;
  border-bottom: 1px solid #3e455d;
}

.button-container {
  height: 100px;
  display: flex;
  align-items: center;
}

.point-container h2 {
  font-size: 48px;
  margin: 36px;
  text-align: center;
}

.point-container h3,
.timer-container h3 {
  font-size: 24px;
  font-weight: lighter;
  width: 100%;
  padding-top: 16px;
}

.point-container,
.timer-container,
.button-container {
  position: relative;
  width: 90%;
}

.exit-button {
  font-size: 16px;
  width: 100%;
  padding-top: 3px;
  border: 1px solid #3e455d;
}

.exit-button:hover {
  border-color: #1fa59d;
}

.timerStyle {
  position: absolute;
  top: 64px;
  left: -8px;
  font-size: 48px;
}

.timerStyle h2 {
  display: block;
  width: 200px;

  font-size: 48px;
  text-align: center;
  margin: auto;
}

